@import url(https://fonts.googleapis.com/css2?family=Finlandica:ital,wght@0,400;0,500;0,700;1,400&display=swap);
*:where(:not(html,iframe,canvas,img,svg,video):not(svg *,symbol *)){all:unset;display:revert}*,*::before,*::after{box-sizing:border-box}a,button{cursor:revert}ol,ul,menu{list-style:none}img{max-width:100%}table{border-collapse:collapse}input,textarea{-webkit-user-select:auto}textarea{white-space:revert}meter{-webkit-appearance:revert;appearance:revert}::-webkit-input-placeholder{color:unset}:-ms-input-placeholder{color:unset}::placeholder{color:unset}:where([hidden]){display:none}:where([contenteditable]:not([contenteditable="false"])){-moz-user-modify:read-write;-webkit-user-modify:read-write;overflow-wrap:break-word;-webkit-line-break:after-white-space;-webkit-user-select:auto}:where([draggable="true"]){-webkit-user-drag:element}

body{font-size:16px;font-family:"Finlandica", Arial, sans-serif}.app{padding:0 40px;color:#282a2d;background-color:#e5e9f1}.app__title{margin-bottom:30px;font-size:32px;font-weight:bold;text-align:center}

.comment-info{display:flex;flex-direction:column;grid-gap:10px;gap:10px}.comment-info__email{font-size:12px}.comment-info__title{font-weight:500}

.comment-list{display:flex;flex-direction:column;grid-gap:10px;gap:10px}.comment-list__comment{padding:10px;border:1px solid #dcdcdc;border-radius:20px;transition:border-color 200ms}.comment-list__comment:hover{border-color:#5089c6}

.user-info{display:flex;align-items:center}.user-info__avatar{height:40px;width:40px;margin:5px}.user-info__username{font-style:italic}.user-info__email{font-size:12px}

.post{display:flex;flex-direction:column;grid-gap:10px;gap:10px;max-width:1024px;padding:30px;border-radius:20px;background-color:#fefefe;transition:box-shadow 200ms, -webkit-transform 200ms;transition:transform 200ms, box-shadow 200ms;transition:transform 200ms, box-shadow 200ms, -webkit-transform 200ms}.post:hover{-webkit-transform:scale(1.05);transform:scale(1.05);box-shadow:0 0 20px 10px rgba(0,0,0,0.05)}.post__shifted{margin-left:50px}.post__title{font-size:18px;line-height:1.4;font-weight:500}.post__comments-title{font-weight:500}

.posts-list{display:flex;flex-direction:column;align-items:center;grid-gap:22px;gap:22px}

